<template>
	<div class="company body">
		<div class="body_header flex_start">
			<i class="icon iconfont icon-gongsixiangqing1"></i>
			<span>公司详情</span>
		</div>
		<div class="company_name flex_between">
			<div class="flex_start">
				<div class="company_name_left">
					<i class="icon iconfont icon-gongsixiangqing"></i>
					<span>{{ companyInfo.company_name }}</span>
				</div>
				<div @click="changeEditStatus" v-if="isDisabledEditable" class="company_name_right cursor">
					<span>编辑</span>
					<i class="icon iconfont icon-bianji"></i>
				</div>
			</div>
			<div class="">
				<!-- <div class="download blue_btn cursor flex_center">
					<i class="icon iconfont icon-gongsiyunhang"></i>
					<span>公司运行</span>
				</div> -->
				<div  @click="toFundRunPage" class="table-group-title-btn btn-blue">
					公司运行
				</div>
			</div>
		</div>
		<div class="company_info form">
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					管理公司全称
				</div>
				<div class="company_info_list_right">
					<el-input ref="input1" v-model="companyInfo.company_name" :disabled="true" placeholder="" clearable>
					</el-input>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					管理总规模（人民币/万元）
				</div>
				<div class="company_info_list_right">
					<el-input v-model="companyInfo.fund_amount" :disabled="true" placeholder="" clearable>
					</el-input>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					管理公司简称
				</div>
				<div class="company_info_list_right">
					<el-input ref="input1" v-model="companyInfo.short_name" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
					</el-input>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					所属地区
				</div>
				<div class="company_info_list_right">
					<el-input v-model="companyInfo.area" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
					</el-input>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					备案状态
				</div>
				<div class="company_info_list_right">
					<el-select v-model="companyInfo.record_state" clearable placeholder="请选择" :disabled="isDisabledEditable">
				    		<el-option v-for="item in allFilingStatus" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					统一社会信用代码
				</div>
				<div class="company_info_list_right">
					<el-input v-model="companyInfo.credit_code" :disabled="true" placeholder="" clearable>
					</el-input>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					在管基金数量
				</div>
				<div class="company_info_list_right">
					<el-input v-model="companyInfo.fund_count" :disabled="true" placeholder="" clearable>
					</el-input>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					数据创建人
				</div>
				<!-- @change='chooseCreater' -->
				<div class="company_info_list_right">
					<el-select ref="selectProvince" v-model="companyInfo.create_user_id" clearable filterable placeholder="请选择" :disabled="isDisabledEditable" >
				    		<el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					法定代表人
				</div>
				<div class="company_info_list_right">
					<el-input v-model="companyInfo.legal_representative" :disabled="true" placeholder="" clearable>
					</el-input>
				</div>
			</div>
			
			
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					成立时间
				</div>
				<div class="company_info_list_right">
					<el-date-picker v-model="companyInfo.founding_time" type="date" :disabled="true" placeholder="" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
					</el-date-picker>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					注册资本
				</div>
				<div class="company_info_list_right">
					<el-input v-model="companyInfo.reg_capital" :disabled="true" placeholder="请输入内容" clearable>
					</el-input>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					私募基金管理人编码
				</div>
				<div class="company_info_list_right">
					<el-input v-model="companyInfo.manager_code" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
					</el-input>
				</div>
			</div>
			<div class="company_info_list flex_start">
				<div class="company_info_list_left">
					运营风控负责人
				</div>
				<div class="company_info_list_right">
					<el-select v-model="companyInfo.windcontrol_userid" clearable  filterable placeholder="请选择" :disabled="isDisabledEditable" >
				    		<el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			
			<div v-if="!isDisabledEditable" class="company_info_edit_btn flex_end">
				<button @click="cancelEdit">取消</button>
				<button @click="saveSubmit">保存</button>
			</div>
		</div>
		<!--公司工商信息模块开始-->
		<div class="company_background">
			<div class="block_title flex_between">
				<div class="title_left flex_start">
					<div class="radio"> </div>
					<div class="block_name">
						管理公司信息
					</div>
				</div>
				<div class="title_right flex_end">
					<div class="download cursor flex_center hover_btn" @click="downloadEquityInfo">
						<span class="iconfont icon-daoru cursor"></span>
						<span>下载股权穿透表</span>
					</div>
					<div class="download blue_btn cursor flex_center" @click="downLoadCompanyInfo">
						<span class="iconfont icon-daoru cursor"></span>
						<span>导出企业信息</span>
					</div>
				</div>
			</div>
			<company-detail-background type="1"></company-detail-background>
		</div>
	</div>
</template>

<script>
	import companyDetailBackground from '../../pages/companyBackground/companyDetailBackground.vue'
	import { dialogConfirm, dialogConfirmHasbtn } from "../../config/mUtils";
	import api from "../../config/url"; //引用url.js
	import { getCompanyDetailApi,getUserlistApi,getBeiAnStatusApi,saveCompanyInfoApi,downloadEquityInfoApi } from '../../config/api'
	
	export default {
		name: 'manageCompanyDetail',
		data() {
			return {
				id: '',
				isHaveEditePower: 0,
				isDisabledEditable: true,
				allFilingStatus: [],
				allUserList: [],
				companyInfo: {},
				companyInfoInit: {},
				// 表单字段开始
				company_name: '',
				short_name: '',
				fund_amount: '',
				area: '',
				record_state: '',
				credit_code: '',
				fund_count: '',
				create_user_id: '',
				create_user_name: '',
				legal_representative: '',
				founding_time: '',
				// 表单字段结束
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getAllUserList()
			this.getBeiAnStatus()
			this.getCompanyDetail()
		},
		mounted() {},
		components: {
			companyDetailBackground
		},
		methods: {
			getCompanyDetail(){
				getCompanyDetailApi({
					id: this.id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.isHaveEditePower = res.data.payload.is_edit;
						var data = res.data.payload.sg_company_manager;
						this.companyInfoInit = Object.assign({}, data);
						this.companyInfo = res.data.payload.sg_company_manager;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allUserList = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取备案状态
			getBeiAnStatus(){
				getBeiAnStatusApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allFilingStatus = res.data.payload.record_state;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			changeEditStatus() {
				this.isDisabledEditable = false;
				this.$nextTick(function () {
		        		this.$refs.input1.$el.querySelector('input').focus();
		        });
			},
			cancelEdit(){
				this.companyInfo = Object.assign({}, this.companyInfoInit)
				this.isDisabledEditable = true;
			},
			saveSubmit(){
				if (this.companyInfo.company_name) {
					saveCompanyInfoApi({
						id: this.id,
						company_name: this.companyInfo.company_name,
						short_name: this.companyInfo.short_name,
						area: this.companyInfo.area,
						record_state: this.companyInfo.record_state,
						credit_code: this.companyInfo.credit_code,
						create_user_id: this.companyInfo.create_user_id,
						// create_user_name: this.companyInfo.create_user_name,
						create_user_name:this.$refs.selectProvince.selected.label,
						legal_representative: this.companyInfo.legal_representative,
						founding_time: this.companyInfo.founding_time,
						windcontrol_userid: this.companyInfo.windcontrol_userid,
						manager_code: this.companyInfo.manager_code,
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.isDisabledEditable = true;
							this.getCompanyDetail()
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}else {
					let info = '管理公司全称不能为空';
					dialogConfirm("info", info);
				}
				
				
			},
			downLoadCompanyInfo(){
				var key = window.localStorage.getItem("htcx_server_key");
				let href = api.sourceDataUrl + "/web/Company/CompanyInfoExcel?company_id=" + this.company_id + '&key=' + key + '&new_type=' + 1;
				// //console.log(href)
				window.location.href = href;
			},
			downloadEquityInfo(){
				downloadEquityInfoApi({
					company_name: this.companyInfo.company_name
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			chooseCreater(e){
				this.$nextTick(function () {
					this.create_user_name = this.$refs.selectProvince.selected.label;
		        });
			},
			//去公司运行
			toFundRunPage(){
				this.$router.push({
					name: 'manageCompanyRun',
					query: {
						id: this.id
					}
				})
			},
		},
	}
</script>

<style scoped="scoped">
	.company {
		box-sizing: border-box;
		padding: 0 30px;
		font-size: 14px;
	}
	
	.company_header {
		color: #6292F4;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_header span {
		display: inline-block;
		padding: 0 15px 10px 15px;
		border-bottom: 2px solid #6292F4;
	}
	
	.company_name {
		padding-bottom: 20px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 16px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 5px;
		font-size: 18px;
		color: #6292F4;
	}
	
	.company_name_right {
		margin-left: 80px;
		color: #6292F4;
	}
	
	.company_name_right i,
	.company_name_right span {
		color: #6292F4;
	}
	
	.company_name_right button {
		width: 115px;
		height: 30px;
		margin-left: 37px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	
	.company_info {
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		flex-wrap: wrap;
		padding: 8px 20px 0 20px;
	}
	
	.company_info_list {
		width: 45%;
		display: inline-flex;
		/*float: left;*/
		margin: 15px 0;
	}
	
	.company_info_list_left {
		width: 140px;
		color: #6F7071;
	}
	
	.company_info_list_right {
		width: 250px;
		margin-left: 5px;
		color: #33302D;
	}
	
	.company_info_list_right input {
		width: 170px;
		height: 30px;
		box-sizing: border-box;
		padding: 0 10px;
		border-radius: 20px;
		background: #EAEAEA;
		border: none;
	}
	
	.company_background {
		margin-top: 60px;
	}
	.company_info_edit_btn{
		width: 100%;
		margin-top: 70px;
	}
	.company_info_edit_btn button{
		width: 94px;
		height: 36px;
		color: white;
		border-radius: 20px;
		background: #6292F4;
	}
	.company_info_edit_btn button:nth-child(1){
		margin-right: 28px;
	}
	/*股权穿透图开始*/
	
	.equity_map {
		margin-top: 50px;
	}
	
	.block_title {
		margin-bottom: 25px;
		padding-bottom: 10px;
		border-bottom: 1px solid #CECBC9;
	}
	
	.title_left>div {
		color: #6292F4;
		font-size: 16px;
	}
	
	.title_left .radio {
		width: 7px;
		height: 7px;
		margin-right: 12px;
		border-radius: 50%;
		background: #6292F4;
	}
	
	.title_right>div {
		color: #6292F4;
	}
	
	.title_right i {
		color: #6292F4;
	}
	
	.download {
		width: 160px;
		height: 30px;
		margin-left: 35px;
		border: 1px solid #6292F4;
		border-radius: 20px;
	}
	.download i{
		font-size: 28px;
	}
	.download span {
		color: #6292F4;
	}
	
	.download .iconfont {
		margin-right: 7px;
	}
	
	.switch_label {
		margin-right: 18px;
	}
</style>


